import React from 'react';
import {NavLink, useHistory} from "react-router-dom";
import {
    HeaderWrapper,
    HeaderItem,
    HeaderItemContainer
} from "../../styles/StyledComponents/Header/Header.StyledComponent";
import AccountBoxIcon from '@material-ui/icons/AccountBox';
import {useSelector} from "react-redux";
import {selectAuthUser} from "../../selectors/auth/auth.selector";
import {useAppDispatch} from "../../store";
import {authLogoutAction} from "../../store/actions/auth.action";
import {clearStorage} from "../../common/functions";


export const Header: React.FC = () => {
    const auth = useSelector(selectAuthUser)
    const dispatch = useAppDispatch()
    const history = useHistory()
    const onClick = () => {
        if (auth && auth !== 'unauthorized') {
            clearStorage()
            dispatch(authLogoutAction())
            return
        }
        history.push('/auth/login')
    }
    return (
        <HeaderWrapper>
            <HeaderItemContainer>
                <HeaderItem>
                    <NavLink to={`/user/${auth}`}>Profile</NavLink>
                </HeaderItem>
                <HeaderItem>
                    <NavLink to='/cars/garage'>garage</NavLink>
                </HeaderItem>
                <HeaderItem>
                    <NavLink to='/subscriptions'>subscriptions</NavLink>
                </HeaderItem>
                <HeaderItem>
                    <NavLink to='/search'>Search</NavLink>
                </HeaderItem>
                <HeaderItem onClick={onClick} style={{cursor: 'pointer'}}>
                    <span><AccountBoxIcon/></span>
                    <span>{auth && auth !== 'unauthorized' ? 'logout' : 'login'}</span>
                </HeaderItem>
            </HeaderItemContainer>
        </HeaderWrapper>
    );
};

